<template>
    <div class="app-complex-table"
        :class="meta">
        <app-preloader in-component
            :loading="loading"></app-preloader>

        <template v-if="data && data.length > 0">

            <input class="app-complex-table__search form-control"
                v-model="search"
                type="text"
                placeholder="Enter text...">

            <div class="app-complex-table__table">
                <app-table v-model="currentPaginationData"
                    :sortDir="sort.currentSortDir"
                    :sortName="sort.currentSortName"
                    @addRowHere="addRowHere"
                    @sortData="changeCurrentSort"></app-table>
            </div>

            <div class="app-complex-table__actions">
                <div class="app-complex-table__buttons">
                    <div class="gutter-1">
                        <button class="app-btn btn btn-light"
                            @click="deleteTable">Delete Table</button>
                        <button class="app-btn btn btn-light"
                            @click="clearData(data)">Clear Table</button>
                        <button class="app-btn btn btn-light"
                            @click="openModal">Copy Table</button>
                        <button class="app-btn btn btn-light"
                            @click="restoreFromBuffer">Restore Table</button>
                    </div>
                </div>
                <div class="app-complex-table__pagination">
                    <app-pagination :total="totalItems"
                        @changeCurrentPage="setCurrentPage"></app-pagination>
                </div>
            </div>

            <app-modal v-if="showModal"
                title="Данные таблицы"
                @closeModal="closeModal">
                <pre>
                    <code>
                        {{filterData}}
                    </code>
                </pre>
                <template #footer>
                    <button class="btn btn-light"
                        @click="copyTable(filterData)">Copy</button>
                </template>
            </app-modal>

        </template>

        <template v-else>
            <div class="app-complex-table__no-data">No data</div>
        </template>
    </div>
</template>

<script>
import AppTable from "./AppTable";
import _ from "lodash";
import { setObjectId, flattenObject } from "@/utils/utils.js";

export default {
    name: "AppComplexTable",
    data: function() {
        return {
            data: [],
            showModal: false,
            sort: {
                currentSortName: "id",
                currentSortDir: "asc"
            },
            search: "",
            pagination: {
                currentPage: 1,
                pageSize: 10
            },
            loading: false,
            getDataOnCreate: true
        };
    },
    props: {
        /**
         * Fields array for load data
         */
        fields: {
            type: Array,
            default: () => [
                "id",
                "firstName",
                "lastName",
                "email",
                "phone",
                "adress"
                // "description"
            ]
        },
        /**
         * Count rows of data
         */
        rows: {
            type: Number,
            default: 1
        },
        /**
         * Additional class of styles
         * `app-complex-table--no-head, app-complex-table--odd, app-complex-table--even`
         */
        meta: {
            type: Array,
            default: () => ["app-complex-table--even"]
        }
    },
    created() {
        if (this.getDataOnCreate) {
            this.getData();
        }
    },
    methods: {
        /**
         * Return query object for request.
         * Is called when the set query for getData.
         */
        getData() {
            let app = this;
            return new Promise(resolve => {
                if (app.beforeGetData()) {
                    app.loading = true;
                    let params = new URLSearchParams();

                    let query = app.getQuery();

                    if (query) {
                        app.appendParams(params, query);
                    }

                    app.axios
                        .get(app.$api, { params: params })
                        .catch(function(error) {
                            throw new Error(error);
                        })
                        .then(function(response) {
                            app.$set(app, "data", response.data);
                            app.afterGetData();
                            resolve(app.data);
                            app.loading = false;
                        });
                }
            });
        },

        /**
         * Return boolean value.
         * Is called before getData.
         * @returns {boolean}
         */
        beforeGetData() {
            return true;
        },

        /**
         * Return query object for request.
         * Is called when the set query for getData.
         * @returns {Object} Query object
         */
        getQuery() {
            let app = this;
            let query = {};
            this.fields.forEach(field => {
                query[field] = app.getFieldParams(field);
            });
            query.rows = this.rows;
            return query;
        },

        /**
         * Return param for request item.
         * Is called when the get query for getData.
         * @param {string} item Request item name
         * @returns {string} Field param
         */
        getFieldParams(item) {
            switch (item) {
                case "id":
                    return "{number|1000}";
                case "firstName":
                    return "{firstName}";
                case "lastName":
                    return "{lastName}";
                case "email":
                    return "{email}";
                case "phone":
                    return "{phone|(xxx)xxx-xx-xx}";
                case "adress":
                    return "{addressObject}";
                case "description":
                    return "{lorem|32}";
            }
        },

        /**
         * Is called when set query for getData.
         * Add params for request
         * @param {URLSearchParams} params Array of params for request
         * @param {Object} obj Object of keys for request params
         */
        appendParams(params, obj) {
            for (let key in obj) {
                if (obj.hasOwnProperty(key)) {
                    params.append(key, obj[key]);
                }
            }
        },

        /**
         * Is called when data load.
         * Made object flat for table
         * @param {Object} obj Data object
         */
        flatData(obj) {
            obj.forEach(function(item, index) {
                obj[index] = flattenObject(item);
            });
        },

        /**
         * Is called when click on table header cell and when data loaded.
         * Change current sort in table.
         */
        sortData() {
            this.data = _.orderBy(
                this.data,
                this.sort.currentSortName,
                this.sort.currentSortDir
            );
        },

        /**
         * Is called after get data from request.
         */
        afterGetData() {
            this.flatData(this.data);
            this.sortData(this.sort.currentSortName);
            /**
             * Is called after get data from request.
             * Using this for customize data
             *
             * @event afterGetData
             * @type {object}
             * @property {object} data Table data
             */
            this.$emit("afterGetData", { data: this.data });
        },

        /**
         * Add row after current row and in the end table.
         * @param {number} Id Current unique id from object
         * @param {boolean} clear Clears copied row
         */
        addRowHere(id, clear) {
            let item = {};

            if (id) {
                item = _.cloneDeep(this.data[id - 1]);
                if (clear == true) {
                    this.clearData(item);
                    item.id = 0;
                }
                this.data.splice(id, 0, item);
            } else {
                item = _.cloneDeep(this.data[0]);
                this.clearData(item);
                item.id = 0;
                this.data.push(item);
            }
        },

        /**
         * Is called when pagination page change.
         * @param {nubmer} currentPage Page number
         */
        setCurrentPage(currentPage) {
            this.pagination.currentPage = currentPage;
        },

        /**
         * Is called when the user clicks on the button.
         * Show Modal for copy table.
         */
        openModal() {
            this.showModal = true;
        },

        /**
         * Is called when the user clicks on the button.
         * Hide Modal for copy table.
         */
        closeModal() {
            this.showModal = false;
        },

        /**
         * Is called when the user clicks on the button.
         * Delete Table element from page.
         */
        deleteTable() {
            this.$destroy();
            this.$el.parentNode.removeChild(this.$el);
        },

        /**
         * Is called when the user clicks on the button.
         * Clear data Table
         * @param {Object} obj Object for clearing
         * @param {Array} keys Array of keys for clearing
         */
        clearData(obj, keys) {
            if (obj) {
                Object.keys(obj).forEach(key => {
                    if (typeof obj[key] === "object") {
                        this.clearData(obj[key], keys);
                    } else if (keys && keys.indexOf(key) != -1) {
                        obj[key] = "-";
                    } else if (!keys) {
                        obj[key] = "-";
                    }
                });
            }
        },

        /**
         * Is called when click on table header cell.
         * Change current sort in table.
         * @param {string} sort String for filtering
         */
        changeCurrentSort(sort) {
            if (sort === this.sort.currentSortName) {
                this.sort.currentSortDir =
                    this.sort.currentSortDir === "asc" ? "desc" : "asc";
            }
            this.sort.currentSortName = sort;

            this.sortData();
        },

        /**
         * Is called when click copy table button.
         * Writes Table data in buffer
         * @param {Object} data Table data
         */
        copyTable(data) {
            navigator.clipboard.writeText(JSON.stringify(data)).catch(error => {
                throw new Error(error);
            });
        },

        /**
         * Is called when click restore table button.
         * Reads Table data from buffer and set her.
         */
        restoreFromBuffer() {
            navigator.clipboard
                .readText()
                .then(text => {
                    this.data = JSON.parse(text);
                    this.pagination.currentPage = 1;
                })
                .catch(error => {
                    throw new Error(error);
                });
        },

        /**
         * Is called when data filtering.
         * Set unique Id for everyone object in Data
         * @param {Object} data Table data
         */
        setUniqueData(data) {
            data.forEach(function(item, index) {
                setObjectId(item, index);
            });
        }
    },
    computed: {
        /**
         * Is called when user input search query.
         * @returns {Array} Filtered Array
         */
        filterData() {
            let data = this.data;
            let query = this.search;

            this.setUniqueData(data);

            if (query) {
                data = _.filter(data, function(item) {
                    let fields = Object.values(item);
                    return fields.find(item => {
                        return item
                            .toString()
                            .toLowerCase()
                            .includes(query.toString().toLowerCase());
                    });
                });
            }

            return data;
        },

        /**
         * Retun the number of items finding in finded data.
         * @returns {Number} Items count
         */
        totalItems() {
            return this.filterData.length;
        },

        /**
         * Retun current pagination data to display.
         * @returns {Array} Current pagination items
         */
        currentPaginationData() {
            let currentPage = this.pagination.currentPage;
            const pageSize = this.pagination.pageSize;
            let offset = (currentPage - 1) * pageSize;
            let data = this.filterData;

            if (this.totalItems > pageSize) {
                return _.drop(data, offset).slice(0, pageSize);
            } else {
                return data;
            }
        }
    },
    components: { AppTable }
};
</script>
