<template>
    <div class="app-pagination">

        <div class="app-pagination__total">
            Pages: {{totalPages}}
        </div>
        <div class="app-pagination__list pagination">
            <div v-if="totalPages > 5"
                class="app-pagination__item page-item"
                :class="currentPage == 1 ? 'is-disabled disabled' : ''">
                <div class="app-pagination__btn app-pagination__btn--start page-link"
                    @click="changeCurrentPage(1)">Start</div>
            </div>
            <div class="app-pagination__item page-item"
                :class="currentPage == 1 ? 'is-disabled disabled' : ''">
                <div class="app-pagination__btn app-pagination__btn--prev page-link"
                    @click="changeCurrentPage(currentPage - 1)">Prev</div>
            </div>
            <template v-for="(pageNumber, index) in currentPaginationPages">
                <div class="app-pagination__item page-item"
                    :class="pageNumber == currentPage ? 'is-active active' : ''"
                    :key="index"
                    @click="changeCurrentPage(pageNumber)">
                    <div class="page-link">{{pageNumber}}</div>
                </div>
            </template>
            <div class="app-pagination__item page-item"
                :class="currentPage == totalPages ? 'is-disabled disabled' : ''">
                <div class="app-pagination__btn app-pagination__btn--next page-link"
                    @click="changeCurrentPage(currentPage + 1)">Next</div>
            </div>
            <div v-if="totalPages > 5"
                class="app-pagination__item page-item"
                :class="currentPage == totalPages ? 'is-disabled disabled' : ''">
                <div class="app-pagination__btn app-pagination__btn--end page-link"
                    @click="changeCurrentPage(totalPages)">End</div>
            </div>
        </div>

    </div>
</template>

<script>
export default {
    name: "AppPagination",
    data: function() {
        return {
            currentPage: 1
        };
    },
    props: {
        /**
         * Set items count on one page
         */
        pageSize: {
            type: Number,
            default: 10
        },
        /**
         * Number of items
         */
        total: Number
    },
    watch: {
        total() {
            if (this.total <= this.pageSize) {
                this.changeCurrentPage(1);
            }
        }
    },
    methods: {
        /**
         * Retun the number of pages finding in finded data.
         * @param {number} page Page nubmer to set
         */
        changeCurrentPage(page) {
            this.currentPage = page;
            /**
             * Change current page.
             *
             * @event changeCurrentPage
             * @type {object}
             */
            this.$emit("changeCurrentPage", page);
        }
    },
    computed: {
        /**
         * Retun the number of pages finding in finded data.
         * @returns {number} Pages count
         */
        totalPages() {
            return Math.ceil(this.total / this.pageSize);
        },

        /**
         * Retun all pagination pages.
         * @returns {Array} Pages
         */
        paginationPages() {
            let pages = [];

            for (let index = 1; index <= this.totalPages; index++) {
                pages.push(index);
            }
            return pages;
        },

        /**
         * Retun current pagination pages to display.
         * @returns {Array} Pages
         */
        currentPaginationPages() {
            let pages = this.paginationPages;
            let currentPage = this.currentPage;

            if (currentPage <= 3) {
                return pages.slice(0, 5);
            } else if (currentPage >= pages.length - 2) {
                return pages.slice(-5);
            } else {
                return pages.slice(currentPage - 3, currentPage + 2);
            }
        }
    }
};
</script>