import AppPreloader from './components/AppPreloader.vue';
import AppModal from './components/AppModal.vue';
import AppPagination from './components/AppPagination.vue';
import AppComplexTable from './components/AppComplexTable/AppComplexTable.vue';
import AppTable from './components/AppComplexTable/AppTable.vue';
import AppTableRow from './components/AppComplexTable/AppTableRow.vue';
import AppTableCell from './components/AppComplexTable/AppTableCell.vue';

const Components = {
    AppPreloader,
    AppModal,
    AppPagination,
    AppComplexTable,
    AppTable,
    AppTableRow,
    AppTableCell,
};

function install(Vue) {
    Object.keys(Components).forEach(key => {
        const Component = Components[key];
        Vue.component(Component.name, Component);
    });
}

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

export default {
    install,
    Components,
};
