<template>
    <div class="app-table table-responsive">
        <table v-if="value && value.length > 0"
            class="app-table__table table table-bordered">
            <thead v-if="tableFields"
                class="app-table__head thead-dark">
                <app-table-row v-model="tableFields"
                    key="table-header"
                    table-header
                    :sortDir="sortDir"
                    :sortName="sortName"
                    @sortData="sortData"></app-table-row>
            </thead>
            <tbody v-if="value"
                class="app-table__body">
                <template v-for="(row, index) in value">
                    <app-table-row v-model="value[index]"
                        :key="index"
                        @addRowHere="addRowHere"></app-table-row>
                </template>
            </tbody>
        </table>
        <template v-else>
            <div class="app-table__no-result">No result</div>
        </template>
    </div>
</template>

<script>
import AppTableRow from "./AppTableRow";
import complexTable from "@/mixins/complexTable.js";
import { getObjectId } from "@/utils/utils.js";
export default {
    name: "AppTable",
    computed: {
        /**
         * Retun fields for table header
         * @returns {Array} Fields
         */
        tableFields() {
            return Object.keys(this.value[0]);
        }
    },
    methods: {
        getObjectId: getObjectId
    },
    mixins: [complexTable],
    components: { AppTableRow }
};
</script>
