<template>
    <transition name="fade"
        appear
        @after-enter="show">
        <div class="app-modal"
            tabindex="0"
            @click="hide">
            <transition name="fade"
                appear
                @after-leave="closeModal">
                <div v-if="showContent && this.$slots.default"
                    class="app-modal__wrapper"
                    :style="`max-width: ${width}`"
                    @click.stop>
                    <div class="app-modal__header">
                        <!-- @slot Use this slot header -->
                        <slot v-if="title || this.$slots.header"
                            name="header">{{title}}</slot>
                        <button class="app-modal__close"
                            @click="hide">&#10006;</button>
                    </div>
                    <div class="app-modal__body">
                        <!-- @slot Use this slot default -->
                        <slot></slot>
                    </div>
                    <div class="app-modal__footer">
                        <div class="app-modal__footer-wrapper">
                            <!-- @slot Use this slot footer -->
                            <slot v-if="this.$slots.footer"
                                name="footer"></slot>
                            <button v-if="!noCloseBtn"
                                class="btn btn-link btn-sm"
                                @click="hide">Close</button>
                        </div>
                    </div>
                </div>
            </transition>
        </div>
    </transition>
</template>

<script>
export default {
    name: "AppModal",
    props: {
        /**
         * Set modal title
         */
        title: String,
        /**
         * Set modal width
         */
        width: [String, Number],
        /**
         * Hide close button on moda footer
         */
        noCloseBtn: {
            type: Boolean,
            defaul: false
        }
    },
    data: function() {
        return {
            showContent: false
        };
    },
    methods: {
        /**
         * Called when user click close buttons.
         * Show modal.
         */
        show() {
            const el = document.body;
            el.classList.add("is-fixed");
            this.showContent = true;
        },

        /**
         * Called when user click close buttons.
         * Hide modal.
         */
        hide() {
            const el = document.body;
            el.classList.remove("is-fixed");
            this.showContent = false;
        },

        /**
         * Called when user click close buttons and modal mask
         */
        closeModal(e) {
            /**
             * Close modal event.
             *
             * @event closeModal
             * @type {object}
             * @property {object} e Event
             */
            this.$emit("closeModal", e);
        }
    }
};
</script>