// src/mixins/complexTable.js.js

export default {
    props: {
        /**
         * Model
         */
        value: [Number, String, Array, Object],
        /**
         * For table header
         */
        tableHeader: {
            type: Boolean,
            default: false,
        },
        /**
         * Value for sorting
         */
        sortName: [Number, String],
        /**
         * Direction for sorting
         */
        sortDir: String,
    },
    methods: {
        /**
         * Sort data event.
         *
         * @event sortData
         * @type {object}
         * @property {string} sortName Current sort name
         */
        sortData(sortName) {
            this.$emit('sortData', sortName);
        },

        /**
         * Add row in table event.
         *
         * @event addRowHere
         * @type {object}
         * @property {number} id Current object Id
         * @property {boolean} clear Cleat table or no
         */
        addRowHere(id, clear = false) {
            this.$emit('addRowHere', id, clear);
        },
    },
};
