<template>
    <tr class="app-table-row">
        <template v-for="(item, key, index) in value">
            <app-table-cell class="app-table-row__cell"
                v-model="value[key]"
                :bold="index == 0"
                :edit="true"
                :key="key"
                :sortDir="sortDir"
                :sortName="sortName"
                :table-header="tableHeader"
                @sortData="sortData"></app-table-cell>
        </template>
        <app-table-cell v-if="actions"
            class="app-table-row__cell app-table-row__cell--actions text-center"
            :table-header="tableHeader">
            <template v-if="tableHeader">
                Actions
            </template>
            <template v-else>
                <i class="far fa-copy"
                    title="Copy row here"
                    @click="addRowHere(currentId)"></i>
                <i class="fas fa-plus-square"
                    title="Add row here"
                    @click="addRowHere(currentId, true)"></i>
                <i class="far fa-plus-square"
                    title="Add row end"
                    @click="addRowHere()"></i>
            </template>
        </app-table-cell>
    </tr>
</template>

<script>
import AppTableCell from "./AppTableCell";
import complexTable from "@/mixins/complexTable.js";
import { getObjectId } from "@/utils/utils.js";

export default {
    name: "AppTableRow",
    props: {
        /**
         * Show actions button
         */
        actions: {
            type: Boolean,
            default: true
        }
    },

    computed: {
        /**
         * Retun curreent unique object Ii
         * @returns {number} Id
         */
        currentId() {
            return getObjectId(this.value);
        }
    },
    mixins: [complexTable],
    components: { AppTableCell }
};
</script>