<template>
    <component class="app-table-cell"
        :class="classes(value)"
        :is="tableHeader ? 'th' : 'td'"
        @click="tableHeader ? sortData(value) : ''"
        @dblclick="startEdit">
        <template v-if="canEdit && localEdit">
            <input type="text"
                v-autofocus="true"
                :value="value"
                @blur="stopEdit($event)"
                @input="updateValue($event.target.value)"
                @keydown.enter="stopEdit($event)"
                @keydown.esc="stopEdit($event)">
        </template>
        <template v-else>
            <!-- @slot Use this slot default -->
            <slot>{{value}}</slot>
        </template>
    </component>
</template>

<script>
import complexTable from "@/mixins/complexTable.js";
import autofocus from "@/directives/autofocus";

export default {
    name: "AppTableCell",
    data: function() {
        return {
            localEdit: false,
            oldValue: this.value
        };
    },
    props: {
        edit: {
            type: Boolean,
            default: false
        },
        bold: Boolean
    },
    methods: {
        /**
         * Return Array of classes for table cell
         * @param {string} field Field name
         * @returns {Array} Array of classes for table geader cell
         */
        classes(field) {
            return [
                { "app-table-cell--header": this.tableHeader },
                { "app-table-cell--bold": this.bold },
                this.sortName == field && this.tableHeader
                    ? `app-table-cell--${this.sortDir} is-active`
                    : ""
            ];
        },

        /**
         * Update model value
         * @param {string} value Value
         */
        updateValue(value) {
            this.$emit("input", value);
        },

        /**
         * Get model value on input
         * @param {Object} event Current event
         */
        onInput(event) {
            this.updateValue(event.target.value);
        },

        /**
         * Stop editing and update model value
         * @param {Object} event Current event
         */
        stopEdit(event) {
            if (event.type === "keydown" && event.keyCode === 27) {
                this.updateValue(this.oldValue);
            } else if (event.type === "keydown" && event.keyCode === 13) {
                this.updateValue(event.target.value);
            }
            this.localEdit = false;
        },

        /**
         * Start editing on double click cell
         */
        startEdit() {
            this.oldValue = this.value;
            this.localEdit = this.canEdit ? true : false;
        }
    },
    computed: {
        /**
         * Determines whether the current cell can be edited
         * @returns {boolean} Can edit cell
         */
        canEdit() {
            return !this.tableHeader && this.edit ? true : false;
        }
    },
    directives: { autofocus },
    mixins: [complexTable]
};
</script>